import { Link } from "react-router-dom";
import "./footer.css";
import { useStores } from "stores/root.store";
import { ROUTES } from "const/Constants";

export const Footer = () => {
    const { categoryStore } = useStores();
    return (
        <>
            <div className="footer">
                <div className="container">
                    <div className="footer__innerWrap">
                        <div className="footer__social">
                            <img src="./source/img/logo.svg" alt="Logo" />
                            <div className="footer__fullAddress">
                                350000, Краснодарский край, г. Краснодар, ул. им. Митрофана Седина, 1 ИНН 1596325871,
                                КПП 987456321
                            </div>
                            <img
                                className="socialMediaIcon"
                                src="./source/img/icon/socialMedia.svg"
                                alt="socialMedia"
                            />
                        </div>
                        <div className="footer__contacts">
                            <h4>КОНТАКТЫ</h4>
                            <ul>
                                <li>Краснодар</li>
                                <li>info@dr.smartron.ru</li>
                                <li>8 800 800-80-08</li>
                                <li>8 (999) 999-99-99</li>
                                <li>Краснодар, улица им Митрофана Седина, 1</li>
                            </ul>
                        </div>
                        <div className="footer__catalog">
                            <h4>КАТАЛОГ</h4>
                            <ul>
                                {categoryStore.category.map((cat) => (
                                    <li key={cat.id}>
                                        <Link to={ROUTES.CATALOG + cat.url}>{cat.name}</Link>
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <div className="footer__menu">
                            <h4>МЕНЮ</h4>
                            <ul>
                                <li>
                                    <Link to={ROUTES.HOME}>Главная</Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.WARRANTY}>Гарантия</Link>
                                </li>
                                <li>Каталог товаров</li>
                                <li>Прайс-лист</li>
                                <li>
                                    <Link to={ROUTES.ABOUT}>Гарантия</Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.BUYER}>Скупка дисплеев</Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.DELIVERY}>Доставка и оплата</Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.FEEDBACK}>Отзывы</Link>
                                </li>
                                <li>
                                    <Link to={ROUTES.CONTACTS}>Контакты</Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <div className="container">
                    <div className="privacyPolicy">
                        {" "}
                        <div>Политика конфиденциальности</div>
                        <div>©2000 - 2022 | Все права защищены</div>
                    </div>
                </div>
            </div>
            <div className="mobileNavBarPlug"></div>
        </>
    );
};
