import classes from "./Delivery.module.css";

import { BreadCrumps } from "components/UI/breadCrumps";

export const Delivery = () => {
    return (
        <div className="container">
            <BreadCrumps pathArr={[{ name: "Доставка и оплата" }]} />
            <div className={classes.deliverWrap}>
                <h2 className="h2MarginTop">Доставка и оплата</h2>
                <div className={classes.deliverSection}>
                    <div className={classes.delivery}>
                        <h3>Доставка Dr.Smarton</h3>
                        <h4>Мы отправляем заказы транспортными компаниями:</h4>
                        <ul>
                            <li>Почта России</li>
                            <li>СДЭК</li>
                            <li>Major Express</li>
                        </ul>
                    </div>
                    <div className={classes.pickup}>
                        <h3>Доставка в пункты выдачи транспортных компаний</h3>
                        <h4>Мы отправляем заказы транспортными компаниями:</h4>
                        <ul>
                            <li>СДЭК</li>
                            <li>Боксберри</li>
                        </ul>
                    </div>
                </div>
                <h3>Оплата</h3>
                <ul>
                    <li>Онлайн оплата на сайте</li>
                    <li>Оплата на расчетный счет (для юр. лиц)</li>
                    <li>PayPal</li>
                </ul>
                <p className={classes.attention}>
                    Точная стоимость доставки будет рассчитана при оформлении заказа: она будет зависеть от веса и
                    объема всех товаров в корзине. Оплаченный заказ будет передан в транспортную компанию не позднее,
                    чем на следующий день после поступления оплаты.
                </p>
            </div>
        </div>
    );
};
